// https://css-tricks.com/considerations-styling-modal/
var modals = document.querySelectorAll(".modal");
var modalOverlay = document.querySelector(".modal-overlay");
var closeButtons = document.querySelectorAll(".modal__close-button");
var teamMemberBoxes = document.querySelectorAll(".team-member-box");

modals.forEach(function(modalEl) { 
  modalEl.classList.add("closed");
});
modalOverlay.classList.add("closed");


teamMemberBoxes.forEach(function(teamMemberBox, teamMemberIdx) { 
  teamMemberBox.addEventListener("click", function() {
    modals[teamMemberIdx].classList.toggle("closed");
    modalOverlay.classList.toggle("closed");
  });
});

closeButtons.forEach(function(closeButton, teamMemberIdx){
  closeButton.addEventListener("click", function() {
    modals[teamMemberIdx].classList.toggle("closed");
    modalOverlay.classList.toggle("closed");
  });
});

modalOverlay.addEventListener("click", function() {
    // modal.classList.toggle("closed");
    modals.forEach(function(modal){
      modal.classList.add("closed");
    });
    modalOverlay.classList.toggle("closed");
});


  